<template>
  <div
    class="header"
    :style="
      open ? '' : 'background-image: linear-gradient(90deg, #2aae67, #a7facf)'
    "
  >
    <div style="display: flex; width: 100%">
      <div class="logo" @click="home" v-if="!open">
        <img class="pic" src="../assets/windowLogo.png" alt="" />
        <span style="color: #fff; font-size: 22px">微信专项测试</span>
      </div>
      <div
        style="
          background-color: #2aae67;
          box-sizing: border-box;
          padding-left: 5px;
          cursor: pointer;
        "
        v-else
        @click="home"
      >
        <img class="pic" src="../assets/windowLogo.png" alt="" />
      </div>
      <div
        class="nav"
        :style="
          !open
            ? ''
            : 'background-image: linear-gradient(90deg, #2aae67, #a7facf)'
        "
      >
        <div>
          <div
            v-if="headerName !== '控制台'"
            class="projectnav"
            :style="open ? '' : 'box-shadow: -2px 0px 4px -1px #308a36;'"
          >
            <i
              v-if="
                $route.query.slide !== 'no' &&
                $route.name !== 'QualityReport' &&
                $route.name !== 'Home'
              "
              @click="changeCollapse"
              style="
                margin-right: 10px;
                cursor: pointer;
                font-size: 20px;
                color: #fff;
              "
              :class="!open ? 'el-icon-s-fold' : 'el-icon-s-unfold'"
            ></i>
            <div
              v-if="headerName"
              style="
                color: #fff;
                display: flex;
                align-items: center;
                cursor: pointer;
              "
              @click.prevent="$router.push({ path: '/Console' })"
            >
              控制台
            </div>
            <el-dropdown>
              <span class="el-dropdown-link">
                内测项目<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>黄金糕</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>

            <div
              v-if="$route.name == 'log'"
              style="margin-left: 25px; font-size: 14px"
            >
              <el-popover placement="top-start" trigger="click">
                <span slot="reference"
                  >工具 <i class="el-icon-arrow-down"></i
                ></span>
                <div
                  style="
                    display: flex;
                    flex-wrap: wrap;
                    max-width: 867px;
                    gap: 20px;
                  "
                >
                  <div
                    v-for="(item, i) of select"
                    :key="i"
                    style="
                      display: flex;
                      align-items: center;
                      gap: 10px;
                      width: 250px;
                    "
                  >
                    <i
                      style="font-size: 26px"
                      class="iconfont"
                      :class="item.icon"
                    ></i>
                    <div>
                      <b>{{ item.title }}</b>
                      <p style="margin: 0">{{ item.txt }}</p>
                    </div>
                  </div>
                </div>
              </el-popover>
            </div>
          </div>
        </div>
        <div>
          <span
            @click="log"
            class="wc"
            style="
              color: rgb(106, 108, 115);
              font-size: 14px;
              margin-right: 30px;
              cursor: pointer;
            "
            >进入工作台</span
          >
          <el-dropdown>
            <span class="el-dropdown-link">
              v_jntaodeng<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <!-- <el-dropdown-item>黄金糕</el-dropdown-item> -->
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import { getproject, getcontrolproject } from "@/api/api";
export default {
  props: { isOpen: false, headerName: "", lastProjects: [] },
  data() {
    return {
      open: false,
      project: [],
      searchInput: {
        keyword: "",
      },
      name: "",
      list: [],
      useInfo: {},
      storeProjectID: "",
      lastProject: [],
      date: {
        page_size: 100,
        current_page: 1,
      },
      select: [
        { title: 'For APP（平台云测版）', txt: '面向APP/H5/小程序/小游戏的智能探索型测试服务', icon: 'icon-tupian' },
        { title: 'For APP（本地体验版）', txt: '云真机，全自动，高效率，深触达', icon: 'icon-shouji' },
        { title: 'For H5', txt: '支持Crash/ANR/JSError等专项故障监测', icon: 'icon-shouji' },
        { title: 'For 小程序/小游戏', txt: '支持集过程和结果一体的可视化分析报告', icon: 'icon-shouji' },

        { title: 'For H5', txt: '面向H5的性能测试服务', icon: 'icon-shouji' },
        { title: 'For APP/H5', txt: '面向APP/H5/小程序/小游戏的兼容性测试服务', icon: 'icon-shouji' },
        { title: 'For APP/H5', txt: '支持集过程和结果一体的可视化分析报告', icon: 'icon-shouji' },

        { title: 'For 直播', txt: '视频流回放,黑屏检测,卡顿检测', icon: 'icon-shouji' },
        { title: 'For 短视频', txt: '短视频专项测试', icon: 'icon-weixin' },

        { title: '小游戏云测试', txt: '支持微信小游戏功能、性能、兼容性测试', icon: 'icon-ios' },
        { title: '测试工具', txt: '覆盖主流厂商和头部用户的真机设备', icon: 'icon-ceshi' },
        { title: '报告可视化工具', txt: '提供全面专业的可视化质量报告', icon: 'icon-tupian1' },
      ]
    };
  },
  watch: {
    isOpen: {
      immediate: true,
      deep: true,
      handler(val) {
        this.open = val;
      },
    },

    // 访问不存在的项目时跳转至上一个访问过的项目

    lastProjects: {
      deep: true,
      handler(val) {
        if (val && val.length) {
          console.log(val, "val");
          this.name = val[0].name;
        }
      },
    },
  },
  mounted() {

    this.find();

  },
  methods: {
    log() {
      this.$router.push({ name: "log" });
    },
    //展开导航
    changeCollapse(ev) {
      this.open = !this.open;
      this.$emit("changeCollapse", this.open);
    },
    //退出


    home() {
      return
      this.$router.push({
        path: "/",
      });
    },
    async handleClick(res) {
      if (res) {
        // this.$store.commit('permis/setIs_admin', res.is_admin)
        let lastProject = await getcontrolproject({ id: res.project_id });
        this.$store.commit("switchProjectIDStore", lastProject.data.project_id);
        // this.storeProjectID = this.$store.state.common.switchProjectID;
        this.project.forEach((i) => {
          if (i.is_react_last) {
            return (i.is_react_last = false);
          }
        });

        this.$store.commit("lastProjectName", res.name);
        this.name = res.name;
        this.$emit("handleClick", res);
      }
    },
    lastProjectFilter(data) {
      this.lastProject = data.filter((i) => {
        //头部的项目名称以url的project_id对应

        if (this.$route.params.id) {
          if (this.$route.params.id == i.project_id) {
            i.is_react_last = true;
          } else {
            i.is_react_last = false;
          }
        } else if (this.storeProjectID) {
          //当为分享状态后点击头部的"微信专项测试"，头部的项目名称与公共项目的project_id对应
          if (i.project_id == this.storeProjectID) {
            return (i.is_react_last = true);
          } else {
            i.is_react_last = false;
          }
        }
        if (i.is_react_last) {
          return i.is_react_last;
        }
      });

      this.$store.commit(
        "switchProjectIDStore",
        this.storeProjectID || this.lastProject[0].project_id
      );
    },
    find() {
      let params = {
        page_size: this.date.page_size,
        current_page: this.date.current_page,
        keyword: this.searchInput.keyword,
      };
      return
      getproject(params)
        .then(async (response) => {
          // 保存is_react_last为true的id  表示上一次所访问的项目
          // let is_react_last_id = response.data.find(item => item.is_react_last).project_id
          // this.$store.commit('is_react_last_id', is_react_last_id)

          if (response.code == 500) {
            // return this.$message.warning(response.msg);
          } else {
            this.project = response.data;
            this.$store.commit("projectList", this.project);
            //获取最后访问的项目
            this.lastProjectFilter(this.project);

            // 可视化工具权限的
            const project = this.project.find(item => {
              return item.project_id === this.$store.state.common.lastProjectID
            })
            project && this.$store.commit('permis/setIs_admin', project.is_admin)
          }
        })
        .catch((error) => console.log("getprojectError: " + error));
    },
  },
};
</script>


<style scoped lang="scss">
.header {
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 999;
  height: 55px !important;
  display: flex;
  justify-content: space-between;

  .logo {
    min-width: 240px;
    padding-left: 20px;
    box-sizing: border-box;
    height: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  .nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 1;
    line-height: 55px;
    height: 100%;
    padding-right: 20px;
    box-sizing: border-box;
    color: #fff;
  }

  .pic {
    width: 54px;
  }
  .projectnav {
    float: left;
    height: 55px;
    display: flex;
    align-items: center;
    padding-left: 10px;
  }
  .projectnav .el-dropdown-link {
    color: #fff !important;
  }
}
.wc:hover {
  color: #000 !important;
}
</style>